body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

a {
  color: inherit;
  text-decoration: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*,
html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

* *,
html *,
body * {
  box-sizing: inherit;
}

.available {
  fill: green;
  stroke-width: 3;
  stroke: black;
}

.notAvailable {
  fill: red;
  stroke-width: 3;
  stroke: black;
}

*,
html,
body {
  font-size: 16px;
  font-family: "Roboto", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.25rem;
}

h4 {
  font-size: 1rem;
}

h5 {
  font-size: 0.875rem;
}

h6 {
  font-size: 0.75rem;
}

p {
  font-size: 16px;
  line-height: 1.5;
}

em {
  font-style: italic;
}

strong {
  font-weight: bold;
}

ul {
  list-style: none;
}

a {
  font-weight: inherit;
  text-decoration: none;
}

/*
This file is used to contain all component imports.
Files inside this folder should contain all styles relating
to a reusable component.
*/
.c-formSreach {
  display: grid;
  grid-template-columns: 1fr;
  z-index: 2;
  background-color: #fff;
  padding: 1.25rem;
  border-radius: 0.5rem;
  max-width: 50rem;
  width: 100%;
  border: 1px solid black;
  box-shadow: 0 10px 40px -14px rgba(0, 0, 0, 0.25);
}

.c-formSreach__box {
  padding: 0.3125rem;
  display: grid;
  grid-template-rows: 2fr;
}

.c-formSreach__box label {
  margin-bottom: 0.5rem;
}

.c-formSreach__box input {
  color: #384047;
  background-color: #e8eeef;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.03) inset;
  border-radius: 0.3125rem;
  padding: 1.25rem;
  width: 100%;
  height: 50px;
  border: 1px solid black;
  height: 3.75rem;
}

.c-formSreach__btn {
  padding: 0.3125rem;
}

.c-button {
  text-align: center;
  font-size: 1.125rem;
  color: #fff;
  background-color: #212121;
  width: 100%;
  border-radius: 0.3125rem;
  padding: 1rem;
  cursor: pointer;
  overflow: hidden;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.3);
  text-transform: uppercase;
  border: 1px solid #212121;
  transition: 0.4s all ease-in-out;
}

.c-button__search:hover {
  background-color: #fff;
  color: #212121;
}

.c-button__search--clear {
  background-color: #fff;
  color: #212121;
}

.c-button__search--clear:hover {
  background-color: #212121;
  color: #fff;
}

.c-button__option {
  width: 50%;
  margin: 0 auto;
  font-weight: bold;
}

.c-button__option:hover {
  background-color: #fff;
  color: #212121;
}

.c-button--back {
  display: block;
}

.c-button__login span {
  padding-left: 0.625rem;
}

.c-back {
  z-index: 33;
  position: absolute;
  display: block;
  content: "";
  top: 130px;
  left: 0;
  width: 60px;
  height: 60px;
  cursor: pointer;
  font-size: 3.75rem;
  background-color: white;
  border-radius: 50%;
  transition: 0.4s all ease-in-out;
}

.c-back:hover {
  color: white;
  background-color: black;
  overflow: hidden;
}

.c-flightResult {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template: 400px / 1fr;
  gap: 1.25rem;
  z-index: 2;
  padding-top: 5rem;
  position: relative;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.03) inset;
}

.c-flightResult--option {
  grid-template: 1fr / auto;
}

.c-flightResult__box {
  border: 1px solid #000;
  background-color: #fff;
  padding: 1.25rem;
  border-radius: 0.5rem;
  box-shadow: 0 10px 40px -14px rgba(0, 0, 0, 0.25);
}

.c-flightResult__title {
  text-align: center;
}

.c-flightResult__title span {
  padding: 0 15px;
}

.c-flightResult__text {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  margin: 20px 0;
}

.c-flightResult__text--description {
  margin-bottom: 0.625rem;
  width: 80%;
}

.c-flightResult__text--description span {
  font-weight: bold;
  padding-right: 0.625rem;
}

.c-flightResult__position {
  display: inline-block;
}

.c-flightResult__position--box {
  margin-bottom: 0.9375rem;
  padding: 0.625rem;
  border: 1px solid black;
  border-radius: 0.5rem;
  background-color: #e8eeef;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  color: red;
  font-weight: bold;
}

.c-flightResult__position--box span {
  padding: 0 5px;
  color: black;
}

.c-flightResult__position--box span:nth-child(1) {
  padding-left: 0;
}

.c-flightResult__position--box span svg {
  padding-right: 2%;
  font-size: 1.5rem;
  cursor: pointer;
  transition: 0.4s all ease-in-out;
}

.c-flightResult__position--box span svg:hover {
  transform: rotate(360deg);
}

.c-flightResult__position--box div {
  padding: 5px 20px;
}

.c-flightResult__value {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  margin-bottom: 1.25rem;
  font-size: 1.5rem;
}

.c-flightResult__value span:nth-child(1) {
  text-decoration: underline;
}

.c-flightResult__value--allValue {
  font-size: 2.125rem;
  padding-left: 0.625rem;
  font-weight: bold;
}

.c-airSeatMap {
  border: 1px solid black;
  border-radius: 0.5rem;
  background-color: white;
  height: 100%;
  overflow: auto;
  display: grid;
  justify-content: center;
  grid-row: 1/2;
}

.c-airSeatMap rect {
  cursor: pointer;
}

/*
This file is used to contain all layout imports.
Files inside this folder should contain all styles relating
to layouts.
*/
.l-section {
  background-image: url("./../images/bcg_1.jpg");
  height: 100vh;
  background-size: cover;
  background-position: top;
  overflow: auto;
  display: flex;
}

.l-section:after {
  position: absolute;
  display: block;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}

.l-section--option {
  display: none;
}

.l-section--result {
  display: none;
}

.l-section__container {
  max-width: 1200px;
  width: 90%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  position: relative;
  padding: 130px 0 30px;
  z-index: 3;
}

.l-header {
  background-color: #06038d;
  z-index: 99;
  width: 100%;
  position: fixed;
}

.l-header__container {
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  padding: 1.25rem;
}

.l-header__container img {
  width: 6.25rem;
  cursor: pointer;
}

.l-login {
  position: absolute;
  display: block;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
  max-width: 25rem;
  margin: 0 auto;
  width: 90%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 10px 40px -14px rgba(0, 0, 0, 0.25);
  border-radius: 0.5rem;
}

.l-login__container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 99;
  background-color: #fff;
  padding: 1.25rem;
  border-radius: 0.5rem;
  width: 100%;
  border: 1px solid #000;
}

.l-login__form input {
  color: #384047;
  background-color: #e8eeef;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.03) inset;
  border-radius: 0.3125rem;
  padding: 1.125rem;
  margin-bottom: 1.125rem;
  width: 100%;
  height: 50px;
  border: 1px solid black;
  height: 3.75rem;
}

.l-login__form span {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0.9375rem;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: bold;
}

.l-login__data {
  width: 100%;
  height: 100%;
}

.l-login__data div {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  padding-bottom: 0.9375rem;
}

.l-login__data div span {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin: 0 auto;
  font-size: 1.5rem;
}

.l-login__timer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 3.125rem;
  z-index: 99;
  background-color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0 10px 40px -14px rgba(0, 0, 0, 0.25);
  border: 1px solid black;
}

.l-login__timer span {
  padding-left: 0.9375rem;
}

.l-login__order {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 99;
  background-color: #fff;
  padding: 140px 20px 20px 20px;
  border-radius: 0.5rem;
  box-shadow: 0 10px 40px -14px rgba(0, 0, 0, 0.25);
}

.l-login__order--title {
  margin-bottom: 1.25rem;
}

@media only screen and (min-width: 640px) {
  .c-formSreach {
    grid-template-columns: repeat(2, 50%);
    padding: 2.5rem;
  }
  .c-formSreach__box input {
    margin-bottom: 0.9375rem;
  }
  .c-formSreach__btn {
    padding: 25px 10px 10px 10px;
  }
  .l-header__container img {
    width: 9.375rem;
  }
  .c-flightResult__text {
    grid-template-columns: repeat(2, 2fr);
  }
  .c-flightResult__position--box {
    flex-direction: row;
    align-items: center;
  }
  .c-flightResult__position--box div {
    padding: 0.3125rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@media only screen and (min-width: 1024px) {
  .c-flightResult {
    grid-template: none;
    grid-template-columns: repeat(2, 2fr);
  }
  .c-flightResult__box {
    height: max-content;
  }
  .c-airSeatMap {
    height: 40.625rem;
  }
}
